header,main,nav,footer,figure,figcaption,aside,section,article{display:block}#wrapper{width:55%;font-size:20px;margin:0 auto;min-width:960px;max-width:2048px;box-shadow:3px 3px 3px #ededed;font-family:Bookman Old Style,"Times New Roman",serif}#banner{background-repeat:no-repeat;display:block;margin-left:auto;margin-right:auto;padding-bottom:10px;max-width:100%;height:auto}#code{color:#000;font-family:Consolas,monaco,monospace}.row{margin-right:-15px;margin-left:-15px}.footer{color:#000}::selection{background:#ccc}::-moz-selection{background:#ccc}a{color:#000;text-decoration:none !important;font-family:Georgia,"Times New Roman",Times,serif;font-weight:bold;font-size:large}.column{float:left;width:100%;padding:10px}.left{width:40%}.right{width:40%}body{padding-top:20px;padding-bottom:20px}.row:after{content:"";display:table;clear:both}#nav{font-size:26px;font-family:Bookman Old Style,georgia,serif;text-align:center;text-transform:uppercase;letter-spacing:0px;margin-bottom:20px;background-color:#000;color:#fff;border-color:#ccc !important;border-width:10px;border:solid}.vertical-pipe{color:#ccc}#resume{width:1700px;height:1600px}#cv{display:inline-block;width:700px;height:600px}nav a{color:#fff;font-size:.75em;font-family:Bookman Old Style,"Times New Roman",serif;padding:10px 10px 10px 10px}nav a:hover{color:#ccc}main{background-color:#fff;padding:25px 150px 200px 150px;min-height:550px;display:block;padding-right:25px;padding-left:25px}footer{text-align:center;font-style:italic;font-size:16px;font-family:Bookman Old Style,"Times New Roman",serif;padding:20px;margin-left:30px}footer a:hover{color:#ccc}.job-link{color:#000 !important;font-family:Georgia,"Times New Roman",Times,serif;font-size:medium;font-style:italic;font-weight:normal;text-decoration:none}.job-link:hover{text-decoration:underline !important;font-size:large}#headshot{background-repeat:no-repeat;display:block;margin-left:auto;margin-right:auto;width:80%;border-radius:15px}#niko{background-image:url(image